const StationView = ({
  classes,
  lat,
  lng,
  areaList,
  addStation,
}: Props): React.ReactElement => {
  const [areaName, setAreaName] = React.useState('');
  const [anchorEl, setAnchorEl] = React.useState<SVGSVGElement | null>(null);
  const open = Boolean(anchorEl);
  const id = open ? 'simple-popover' : undefined;

  const handleClick = (event: React.MouseEvent<SVGSVGElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const AreaMenuItems = areaList.map(({name}) => (
    <MenuItem
      key={name}
      value={name}
    >
      {name}
    </MenuItem>
  ));

  const handleAreaChange = (event: any) => {
    setAreaName(event.target.value)
  };

  return (
    <>
      <Popover
        id={id}
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: 'center',
          horizontal: 'right',
        }}
        transformOrigin={{
          vertical: 'center',
          horizontal: 'left',
        }}
      >
        <Paper className={classes.stationContainer}>
          <Typography className={classes.stationTitle}>NY STASJON</Typography>
          <Container className={classes.stationInputContainer} maxWidth="sm">
            <FormControl variant="filled" className={classes.areaSelect}>
              <InputLabel id="choose-area-label">
                Velg område
              </InputLabel>
              <Select
                labelId="choose-area-label"
                id="choose-area-select"
                value={areaName}
                onChange={handleAreaChange}
              >
                {AreaMenuItems}
              </Select>
            </FormControl>
            {/* <TextField
              id="email"
              label="Stasjon navn"
              className={classes.stationInput}
              type="text"
              autoComplete="current-password"
              margin="normal"
              variant="filled"
              // value={email}
              // onChange={event => setEmail(event.target.value)}
              InputLabelProps={{
                classes: {
                  root: classes.stationInputLabel,
                },
              }}
              InputProps={{
                classes: {
                  input: classes.stationInput,
                },
              }}
            />
            <TextField
              id="password"
              label="Enhet navn"
              className={classes.stationInput}
              type="password"
              autoComplete="current-password"
              margin="normal"
              variant="filled"
              // value={password}
              // onChange={event => setPassword(event.target.value)}
              InputLabelProps={{
                classes: {
                  root: classes.stationInputLabel,
                },
              }}
              InputProps={{
                classes: {
                  input: classes.stationInput,
                },
              }}
            /> */}
            <div className={classes.stationButtonGroup}>
              <Button
                variant="contained"
                className={classes.stationButton}
                onClick={() => addStation({
                  name: 'Stasjon',
                  areaName,
                  lat,
                  lng,
                })}
              >
                Legg til
              </Button>
            </div>
          </Container>
        </Paper>
      </Popover>
      <AddLocationIcon
        fontSize={'large'}
        className={classes.stationIcon}
        onClick={handleClick}
      />
    </>
  );
};

