import { connect } from 'react-redux';
import NewStationView from '../components/newStationView';
import { addStation } from '../actions/stationActions';

export default connect(
  (state: any, props) => ({
    areaList: state.station.areaList,
  }),
  (dispatch, props) => ({
    addStation: (station: object) => dispatch(addStation(station)),
  })
)(NewStationView);
