import { connect } from 'react-redux';
import MapView from '../components/mapView';
import * as userActions from '../actions/userActions';
import * as stationActions from '../actions/stationActions';

export default connect(
  (state: any, props) => ({
    stationList: state.station.stationList,
    isAddingStation: state.station.newStation.isAdding,
    newStationLat: state.station.newStation.lat,
    newStationLng: state.station.newStation.lng,
  }),
  (dispatch, props) => ({
    addStationStart: (lat: string, lng: string) => dispatch(stationActions.isAddingStation(true, lat, lng))
  })
)(MapView);
